"use client"
import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import socket from '@/app/socket';
import { useSelector } from 'react-redux';
import { RootState } from '@/app/redux/store';
import Slider from 'react-slick';
import './style.scss'
const Loader = () => {
    const userConfig = useSelector((name: RootState) => name.data);
    const router = useRouter()

    const ConnectToChat = () => {
        socket.emit('createRoom', userConfig);
    }
    useEffect(() => {
        !userConfig.localName ? router.push('/') : ConnectToChat();
        socket.on('roomState', data => data > 1 && router.push('/chat'));
        return () => {
            socket.off('roomState')
        }
    }, [router]);
    const logos = [
        { src: '2', alt: 'Partner 1' },
        { src: '4', alt: 'Partner 2' },
        { src: '/logo3.png', alt: 'Partner 3' },
        { src: '/logo4.png', alt: 'Partner 4' },
        { src: '/logo5.png', alt: 'Partner 5' },
        // Добавьте больше логотипов по необходимости
    ];
    const settings = {
        infinite: true, // Бесконечная прокрутка
    speed: 1000, // Скорость анимации (плавность прокрутки)
    autoplay: true, // Автопрокрутка
    autoplaySpeed: 0, // Нулевой интервал для непрерывной прокрутки
    cssEase: 'linear', // Убирает замедление анимации
    slidesToShow: 5, // Сколько слайдов показывать за раз
    slidesToScroll: 1, // Прокручивать 1 слайд за раз
    pauseOnHover: false, // Не останавливать прокрутку при наведении
        responsive: [
            {
                breakpoint: 768, // Для мобильных устройств
                settings: {
                    slidesToShow: 2, // Показывать 2 логотипа на мобильных
                },
            },
            {
                breakpoint: 480, // Для очень маленьких экранов
                settings: {
                    slidesToShow: 1, // Показывать 1 логотип
                },
            },
        ],
    };
    return (
        userConfig.localName && (
            <>
                <div className="container">
                    <div className="loader">
                        <p>Соединение с чатом устанавливается. Скоро начнём!</p>
                    </div>
                    <Slider {...settings}>
                        {logos.map((logo, index) => (
                            <div key={index} className="logo-slide">
                                <span>{logo.src}</span>
                            </div>
                        ))}
                    </Slider>

                </div>
            </>
        )

    )
}
export default Loader;